@import '@';

.Section {
  padding: $unit;
}

.description {
  max-width: $prose-max-width;
  margin: 0 auto;
  background: $foreground-color;
  padding: 1rem;
  box-shadow: $box-shadow;
}

.thumbs {
  @include o-list-unstyled;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  li {
    -ms-flex-preferred-size: 20%;
        flex-basis: 20%;
    background: $foreground-color;
    margin: $unit;
    //border: 1px dashed rgba(0,0,0,0.125);
    box-shadow: $box-shadow;
    @include o-media(m) {
      -ms-flex-preferred-size: 45%;
          flex-basis: 45%;
    }
    @include o-media(s) {
      -ms-flex-preferred-size: 100%;
          flex-basis: 100%;
    }
    &.limiting {
      -ms-flex-preferred-size: 44%;
          flex-basis: 44%;
      @include o-media(s) {
        -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
      }
    }
  }
}



