@import '@';

$time: 1s;
$flip-time: $time/2;
//$image-size: $unit*16;
$image-size: 256px;

.FlippyThumb {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  overflow: hidden;
  box-shadow: $box-shadow;
  &:hover {
    .front {
      -webkit-transform: rotateY(180deg);
              transform: rotateY(180deg);
      opacity: 0.2;
    }
    .back {
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
}

.front {
  -webkit-transition: opacity $flip-time,
    background-image $time,
    -webkit-transform $flip-time ease-in;
  transition: opacity $flip-time,
    background-image $time,
    -webkit-transform $flip-time ease-in;
  transition: opacity $flip-time,
    transform $flip-time ease-in,
    background-image $time;
  transition:
    opacity $flip-time,
    transform $flip-time ease-in,
    background-image $time,
    -webkit-transform $flip-time ease-in;
  width: $image-size;
  height: $image-size;
  margin: 0 auto;
  box-shadow: $box-shadow;
  background-size: $image-size;
}

.back {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition: opacity $flip-time,
    -webkit-transform $flip-time;
  transition: opacity $flip-time,
    -webkit-transform $flip-time;
  transition: opacity $flip-time,
    transform $flip-time;
  transition:
    opacity $flip-time,
    transform $flip-time,
    -webkit-transform $flip-time;
  -webkit-transition-delay: $flip-time/3;
          transition-delay: $flip-time/3;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
  overflow: hidden;
  .backText {
    width: 50%;
  }
}



