@import '@';

.Modal {
  @include o-size(100%);
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  z-index: 999;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: $unit;
  &.active {
    display: block;
  }
}

.close {
  float: right;
  color: $text-color-light;
}

.header {
  width: 100%;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
}

.main {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}

.footer {
  dummy: 0;
}



